import React from "react"
import { Box, ListItem, List } from "@chakra-ui/react"
import { Link as GatsbyLink } from "gatsby"
const Toc = ({ data, location }) => {
  const list = data.headings

  return (
    <Box className="table-of-contents">
      {list.length > 1 && (
        <List mb={10}>
          {list
            .filter(item => item.depth > 1)
            .map(item => (
              <ListItem
                key={item.id}
                mb={item.depth > 2 ? 1 : 3}
                ml={item.depth > 2 ? 4 : 0}
                color={location.hash === `#${item.id}` ? "rlBlue" : "rlGrey"}
                _hover={{
                  color: "rlBlue",
                }}
              >
                <GatsbyLink to={`#${item.id}`}>{item.value}</GatsbyLink>
              </ListItem>
            ))}
        </List>
      )}
    </Box>
  )
}

export default Toc
