import React from 'react';
import { Heading } from '@chakra-ui/react';
import MenuItem from './menuItem';

const Menu = ({
  content,
  sectionName,
  location,
  urlPrefix,
  isAutogenerated,
}) => {
  const baseDirectory = location.pathname
    .replace(`${urlPrefix}`, '')
    .replaceAll('//', '/')
    .split('/')
    .filter(x => x !== '' && x != null)[0];

  return (
    <nav className="navigation">
      <Heading fontSize={20} fontWeight={700} pb={2.5}>
        {sectionName}
      </Heading>
      <MenuItem
        items={content}
        menuPathPrefix={
          isAutogenerated ? `${urlPrefix}` : `${urlPrefix}/${baseDirectory}`
        }
        level={0}
        location={location}
      />
    </nav>
  );
};

export default Menu;
