import { Link as ChakraLink, Text } from '@chakra-ui/react';
import { Link as GatsbyLink } from 'gatsby';
import React from 'react';
import { globalHistory } from '@reach/router';
import { urlRewrite } from '../utils/urlHelpers';

const Link = ({ children, ...props }) => {
  const { isExternalLink, url } = urlRewrite(
    props.href,
    globalHistory.location,
    props.urlPrefix,
  );

  return (
    <>
      {isExternalLink ? (
        <ChakraLink color="rlBlue" href={url}>
          {children}
        </ChakraLink>
      ) : (
        <Text as="span" color="rlBlue">
          <GatsbyLink
            to={url}
            style={{
              color: 'rlBlue',
            }}
          >
            {children}
          </GatsbyLink>
        </Text>
      )}
    </>
  );
};

export default Link;
