import React from "react"
import { Tabs } from "@chakra-ui/react"
import { useLocation } from "@reach/router"

const UrlControlledTabs = ({ children }) => {
  const location = useLocation()
  const hashRoute = location.hash
  const urlTabNumber = parseInt(hashRoute.replace("#", "")) || 0

  return (
    <Tabs variant="accentColor" defaultIndex={urlTabNumber}>
      {children}
    </Tabs>
  )
}

export default UrlControlledTabs
