import * as React from 'react';
import Layout from '../components/layout';
import Seo from '../components/seo';
import { graphql } from 'gatsby';

import DocumentationPage from './documentation-page';
import HomePage from './home-page';

const BasePage = ({ data, location, pageContext }) => {
  const page = data.pageContent;

  const pageType = page.fileAbsolutePath.includes(
    `/${process.env.GATSBY_HOMEPAGES_FOLDER}/`,
  )
    ? 'homePage'
    : 'docPage';

  return (
    <Layout
      versions={pageContext.versions}
      currentVersion={pageContext.currentVersion}
      currentRepo={pageContext.currentRepo}
      location={location}
    >
      {pageType === 'docPage' ? (
        <DocumentationPage
          data={data}
          location={location}
          urlPrefix={pageContext.urlPrefix}
          currentVersion={pageContext.currentVersion}
          currentRepo={pageContext.currentRepo}
          tocForCurrentSection={pageContext.tocForCurrentSection}
        />
      ) : (
        <HomePage page={page} urlPrefix={pageContext.urlPrefix} />
      )}
    </Layout>
  );
};

export const Head = ({ pageContext, location }) => (
  <Seo
    title={pageContext.title}
    location={location}
    description={pageContext.description}
    prod={pageContext.currentRepo.name}
    ver={pageContext.currentVersion.name}
  />
);

export const query = graphql`
  query ($absolutePath: String!, $tocRegex: String!) {
    pageContent: markdownRemark(fileAbsolutePath: { eq: $absolutePath }) {
      html
      htmlAst
      fileAbsolutePath
      frontmatter {
        title
      }
      headings {
        id
        value
        depth
      }
    }
    toc: markdownRemark(fileAbsolutePath: { regex: $tocRegex }) {
      html
      htmlAst
      fileAbsolutePath

      frontmatter {
        title
      }
    }
  }
`;

export default BasePage;
