import React, { useEffect, useState } from 'react';
import {
  Box,
  AccordionItem,
  AccordionPanel,
  AccordionButton,
  Accordion,
  AccordionIcon,
} from '@chakra-ui/react';
import { navigate } from 'gatsby';

const MenuItem = ({ items, menuPathPrefix, level, location }) => {
  const [expandedIndex, setExpandedIndex] = useState(-1);

  const setMenuData = menuUrlPath => {
    if (menuUrlPath?.startsWith('#') || !menuUrlPath) {
      return '#';
    } else {
      return `/${menuPathPrefix}/${menuUrlPath}`
        .replace(/\/+/g, '/')
        .replace(/\/$/, '');
    }
  };

  const depthFirstSearch = (node, currentLocation) => {
    if (setMenuData(node.href) === currentLocation) {
      return true;
    }

    for (let child of node.children) {
      const found = depthFirstSearch(child, currentLocation);
      if (found) {
        return true;
      }
    }

    return false;
  };

  /**
   * searches if the children contain the url of the current page
   * @param node
   * @returns {boolean}
   */
  const searchCurrentPath = node => {
    const currentLocation = location.pathname.replace(/\/$/, '');
    return depthFirstSearch(node, currentLocation);
  };

  useEffect(() => {
    for (let index = 0; index < items.length; index++) {
      const found = searchCurrentPath(items[index]);
      if (found) {
        setExpandedIndex(index);
        break;
      }
    }
  }, []);

  const navigateToPage = item => {
    if (item.href && item.href !== '#') {
      navigate(setMenuData(item.href));
    }
  };

  const handleAccordionIconClick = index => {
    setExpandedIndex(prevIndex => (prevIndex === index ? -1 : index));
  };

  return (
    <Accordion index={expandedIndex} allowToggle>
      {items?.map((item, index) => (
        <AccordionItem
          className={expandedIndex === index && 'active'}
          key={item.textElement}
          border="none"
          paddingBottom="4px"
        >
          <AccordionButton
            paddingLeft="0"
            _hover={{
              background: 'transparent',
            }}
            py="0.1em"
          >
            {item.children.length > 0 ? (
              <AccordionIcon
                onClick={() => {
                  handleAccordionIconClick(index);
                }}
              />
            ) : (
              <Box w="1.3em" />
            )}
            <Box
              as="span"
              flex="1"
              pl="0.5em"
              textAlign="left"
              fontWeight={level === 0 ? 600 : null}
              fontSize={20}
              lineHeight={1.2}
              background={
                setMenuData(item.href) === location.pathname.replace(/\/$/, '')
                  ? 'rlGrey20'
                  : 'transparent'
              }
              borderRadius={5}
              _hover={{
                background: item.href && 'rlGrey20',
              }}
              onClick={() => navigateToPage(item)}
            >
              {item.textElement}
            </Box>
          </AccordionButton>
          <AccordionPanel py={0}>
            <MenuItem
              menuPathPrefix={menuPathPrefix}
              items={item.children}
              level={level + 1}
              location={location}
            />
          </AccordionPanel>
        </AccordionItem>
      ))}
    </Accordion>
  );
};

export default MenuItem;
