import React from "react"
import { Flex, Icon, Link, Tooltip, Box } from "@chakra-ui/react"
import { EditIcon, ChatIcon, CopyIcon } from "@chakra-ui/icons"
import useCopyToClipboard from "../../hooks/useCopyToClipboard"

const Feedback = ({ location }) => {
  const copyToClipboard = useCopyToClipboard()
  const copyLink = location.href

  //remove leading and trailing slashes and version from the current url
  const url = location.pathname.replace(/(^\/)|(\/$)/g, "")

  // const repoRoot = `https://github.com/${process.env.GATSBY_DOCUMENTATION_REPO}/edit/${selectedApiVersion?.name}/${process.env.GATSBY_DOCUMENTATION_FOLDER}/${url}.md`
  const repoRoot = `.md`

  return (
    <Box mt={5} className="feedback">
      <Box fontWeight={600} fontSize={20}>
        Leave Feedback
      </Box>
      <Flex gap="4" justifyContent="flex-start" w="100%" mt={4}>
        <Tooltip
          label="Copy Link"
          openDelay={500}
          aria-label="Copy the link to this page"
        >
          <Link
            onClick={() => {
              copyToClipboard(copyLink)
            }}
          >
            <Icon as={CopyIcon} color="rlFeedbackIconGrey" w={5} h={5} />
          </Link>
        </Tooltip>
        <Tooltip label="Edit this page" openDelay={500}>
          <Link
            target="_blank"
            href={repoRoot}
            aria-label="Edit this page in GitHub"
            color="black"
          >
            <Icon as={EditIcon} color="rlFeedbackIconGrey" w={5} h={5} />
          </Link>
        </Tooltip>
        <Tooltip label="Log an issue" openDelay={500}>
          <Link
            target="_blank"
            href={`https://github.com/${process.env.GATSBY_DOCUMENTATION_REPO}/issues/new`}
            aria-label="Log an issue in GitHub"
          >
            <Icon as={ChatIcon} color="rlFeedbackIconGrey" w={5} h={5} />
          </Link>
        </Tooltip>
      </Flex>
    </Box>
  )
}

export default Feedback
