export const isHashRoute = url => {
  return url?.startsWith('#');
};

export const isExternalLink = (url, location) => {
  try {
    return location.origin !== new URL(url, location.origin).origin;
  } catch (e) {
    return false;
  }
};

export const urlRewrite = (url, location, urlPrefix) => {
  if (url == null) {
    console.warn(`Null url in ${location.pathname}`);
    return { url: '', isExternalLink: false };
  }

  if (isHashRoute(url)) {
    return { url: url.replace(/\.md/, ''), isExternalLink: false };
  }

  if (isExternalLink(url, location)) {
    return { url: url, isExternalLink: true };
  }

  if (url.startsWith('/')) {
    return {
      url: `/${urlPrefix}/${url.replace(
        new RegExp(`^\\/${process.env.GATSBY_DOCUMENTATION_FOLDER}`),
        '',
      )}`.replace('//', '/'),
      isExternalLink: false,
    };
  }

  const parentRoute = location.pathname.replace(/\/[^/]*(\/)?$/, '');

  const rewrittenInternalLink = `/${parentRoute}/${url
    .replace(new RegExp(`^\\/${process.env.GATSBY_DOCUMENTATION_FOLDER}`), '')
    .replace(/\.md/, '')}`.replace(/\/+/g, '/');

  return { url: rewrittenInternalLink, isExternalLink: false };
};
