import { InfoIcon, WarningTwoIcon } from "@chakra-ui/icons"
import NoteIcon from "../../icons/noteIcon"
import LightBulbIcon from "../../icons/lightBulbIcon"

/**
 * the attributes for every type of callout
 */
const calloutVariations = {
  "[!note]": {
    icon: NoteIcon,
    accentColor: "rlBlue",
    baseColor: "rlBlue10",
    iconColor: "rlBlue",
    title: "Note",
  },
  "[!info]": {
    icon: NoteIcon,
    accentColor: "rlBlue",
    iconColor: "rlBlue",
    baseColor: "rlBlue10",
    title: "Note",
  },
  "[!caution]": {
    icon: InfoIcon,
    accentColor: "rlYellow",
    iconColor: "rlYellow",
    baseColor: "rlYellow10",
    title: "Caution",
  },
  "[!warning]": {
    icon: WarningTwoIcon,
    accentColor: "rlRed",
    iconColor: "rlRed",
    baseColor: "rlRed10",
    title: "Warning",
  },
  "[!danger]": {
    icon: WarningTwoIcon,
    accentColor: "rlRed",
    iconColor: "rlRed",
    baseColor: "rlRed10",
    title: "Warning",
  },
  "[!tip]": {
    icon: LightBulbIcon,
    accentColor: "rlGreen",
    iconColor: "rlGreen",
    baseColor: "rlGreen10",
    title: "Tip",
  },
}

const getCalloutVariationByType = type => {
  const variation = calloutVariations[type]

  //if the variation name is valid, return the corresponding variation data, otherwise return the first variation so that the code doesn't break
  if (variation != null) {
    return variation
  } else {
    const defaultVariationType = Object.keys(calloutVariations)[0]
    return calloutVariations[defaultVariationType]
  }
}

export { calloutVariations, getCalloutVariationByType }
