import React, { useMemo } from 'react';
import {
  Box,
  BreadcrumbItem,
  BreadcrumbLink,
  Breadcrumb,
  Text,
} from '@chakra-ui/react';
import { ChevronRightIcon } from '@chakra-ui/icons';
import theme from '../../@chakra-ui/gatsby-plugin/theme';
import { navigate } from 'gatsby';

const Breadcrumbs = ({
  tocData,
  location,
  urlPrefix,
  isAutogenerated,
  currentRepo,
  sectionName,
}) => {
  const findPathByHref = (tree, targetHref, currentPath = []) => {
    for (const item of tree) {
      const newPath = [...currentPath, item];

      if (item.href === targetHref) {
        return newPath;
      }

      if (item.children && item.children.length > 0) {
        const foundPath = findPathByHref(item.children, targetHref, newPath);
        if (foundPath) {
          return foundPath;
        }
      }
    }

    return null;
  };

  const breadcrumbPath = useMemo(() => {
    const baseDirectory = location.pathname
      .replace(`${urlPrefix}`, '')
      .replaceAll('//', '/')
      .split('/')
      .filter(x => x !== '' && x != null)[0];

    const locationWithoutPrefix = location.pathname
      .replace(
        `/${urlPrefix}${isAutogenerated ? '' : '/' + baseDirectory}/`,
        '',
      )
      .replace(/\/$/, '');

    return findPathByHref(tocData, locationWithoutPrefix);
  }, [location, tocData]);

  const maxNumberOfBreadcrumbsToShow = 4;

  const computeCrumbProps = (crumbIndex, breadcrumbs) => {
    let props = {};

    if (crumbIndex > 1) {
      props = {
        ...props,
        fontWeight: 'bold',
        style: {
          pointerEvents: 'none',
          cursor: 'default',
          textDecoration: 'none',
        },
      };
    }

    if (crumbIndex === breadcrumbs.length - 1) {
      props = {
        ...props,
        color: theme.colors.rlBlack,
      };
    } else {
      props = {
        ...props,
        color: theme.colors.rlGreyDarker,
      };
    }

    return props;
  };

  const generateBreadcrumbs = () => {
    const landingPageBreadcrumb = { href: '/', textElement: 'Solutions' };
    const productPageBreadcrumb = {
      href: `/${urlPrefix}`,
      textElement: currentRepo.name,
    };
    const sectionBreadcrumb = {
      href: '#',
      textElement: sectionName,
    };

    const crumbs = [
      landingPageBreadcrumb,
      productPageBreadcrumb,
      sectionBreadcrumb,
      ...(breadcrumbPath ?? []),
    ];

    let uiBreadcrumbs = crumbs?.map((crumb, index) => (
      <BreadcrumbItem key={index}>
        <BreadcrumbLink
          onClick={e => {
            navigate(crumb.href);
            e.preventDefault();
          }}
          {...computeCrumbProps(index, crumbs)}
        >
          <Text casing="capitalize" noOfLines={1}>
            {crumb.textElement}{' '}
          </Text>
        </BreadcrumbLink>
      </BreadcrumbItem>
    ));

    const elipsisBreadcrumb = (
      <BreadcrumbItem key="elipsisBreadcrumb">
        <Text color={theme.colors.rlGreyDarker}>...</Text>
      </BreadcrumbItem>
    );

    if (uiBreadcrumbs?.length > maxNumberOfBreadcrumbsToShow) {
      const elementsToRemove =
        uiBreadcrumbs.length - maxNumberOfBreadcrumbsToShow;

      //remove breadcrumbs so that they fit in the UI and replace with an ellipsis breadcrumb
      uiBreadcrumbs.splice(2, elementsToRemove);
      uiBreadcrumbs.splice(2, 0, elipsisBreadcrumb);
    }

    return uiBreadcrumbs;
  };

  return (
    <Box hideBelow="md" maxW="100%" overflowX="hidden">
      <Breadcrumb
        separator={<ChevronRightIcon color={theme.colors.rlGreyDarker} />}
      >
        {generateBreadcrumbs()}
      </Breadcrumb>
    </Box>
  );
};
export default Breadcrumbs;
